import React from 'react'
import './styles.module.css'
import {Dprompts, DReplies, DnotFound} from './DefaultConstants'

 const ReactChatBot = ({ PromptsBot, RepliesBot, notFoundBot, botIcon, userIcon  }) => {
  let
    prompts = PromptsBot ? props.Prompts : Dprompts, 
    Replies = RepliesBot ? props.Replies : DReplies,
    notFound = notFoundBot ? props.notFound : DnotFound,
    input = '',
    text = '',
    product = '';
  window.onload = function() {
    let inputField = document.getElementById("input");
    inputField.addEventListener("keydown", (e) => {
      if (e.code === "Enter") {
        input = inputField.value;
        inputField.value = "";
        output();
      }
    });
  }
    
  function output() {
    let product2;
    let text2 = input.toLowerCase().replace(/[^\w\s]/gi, "").replace(/[\d]/gi, "").trim();
    text2 = text2
      .replace(/ a /g, " ") 
      .replace(/i feel /g, "")
      .replace(/whats/g, "what is")
      .replace(/please /g, "")
      .replace(/ please/g, "")
      .replace(/r u/g, "are you");
    text = text2;
    if (compare()) { 
      product2 = compare();
    } else if (text2.match(/thank/gi)) {
      product2 = "You're welcome!"
    } else {
      product2 = notFound[Math.floor(Math.random() * notFound.length)];
    }
    product = product2;
    addChat();
  }

  function compare() {
    let reply;
    let replyFound = false;
    for (let x = 0; x < prompts.length; x++) {
      for (let y = 0; y < prompts[x].length; y++) {
        if (prompts[x][y] === text) {
          let replies = Replies[x];
          reply = replies[Math.floor(Math.random() * replies.length)];
          replyFound = true;
          break;
        }
      }
      if (replyFound) {
        break;
      }
    }
    return reply;
  }

  function addChat() {
    const messagesContainer = document.getElementById("messages");
    let userDiv = document.createElement("div");
    userDiv.id = "user";
    userDiv.className = "user response";
    userDiv.innerHTML = `<img src="`+ userIcon +`" className="avatar"><span>${text}</span>`;
    messagesContainer.appendChild(userDiv);

    let botDiv = document.createElement("div");
    let botImg = document.createElement("img");
    let botText = document.createElement("span");
    botDiv.id = "bot";
    botImg.src = botIcon;
    botImg.className = "avatar";
    botDiv.className = "bot response";
    botText.innerText = "...";
    botDiv.appendChild(botText);
    botDiv.appendChild(botImg);
    messagesContainer.appendChild(botDiv);
    messagesContainer.scrollTop = messagesContainer.scrollHeight - messagesContainer.clientHeight;


    setTimeout(() => {
      botText.innerText = `${product}`;
    }, 2000
    )

  }
		
		return (
      <div className="page-content page-container" id="page-content">
      <div className="padding">
        <div className="row container d-flex justify-content-center">
          <div className="col-md-6">
            <div className="card card-bordered">
              <div className="card-header">
                <h4 className="card-title"><strong>Chat</strong></h4> <a className="btn btn-xs btn-secondary" href="#" data-abc="true">Let's Chat App</a>
              </div>
              <div className="ps-container ps-theme-default ps-active-y" id="chat-content" style="overflow-y: scroll !important; height:400px !important;">
                <div id="messages" classNameName="media media-chat"> 
                </div>
                {/*<div className="media media-chat"> 
                  <img className="avatar" src="https://img.icons8.com/color/36/000000/administrator-male.png" alt="..."/>
                  <div className="media-body">
                    <p>Hi</p>
                    <p>How are you ...???</p>
                    <p>What are you doing tomorrow?<br/> Can we come up a bar?</p>
                    <p className="meta"><time datetime="2018">23:58</time></p>
                  </div>
                </div>
                <div className="media media-meta-day">Today</div>
                <div className="media media-chat media-chat-reverse">
                  <div className="media-body">
                    <p>Hiii, I'm good.</p>
                    <p>How are you doing?</p>
                    <p>Long time no see! Tomorrow office. will be free on sunday.</p>
                    <p className="meta"><time datetime="2018">00:06</time></p>
                  </div>
                </div>*/}
                <div className="ps-scrollbar-x-rail" style="left: 0px; bottom: 0px;">
                  <div className="ps-scrollbar-x" tabindex="0" style="left: 0px; width: 0px;"></div>
                </div>
                <div className="ps-scrollbar-y-rail" style="top: 0px; height: 0px; right: 2px;">
                    <div className="ps-scrollbar-y" tabindex="0" style="top: 0px; height: 2px;"></div>
                </div>
              </div>
              <div className="publisher bt-1 border-light"> <img className="avatar avatar-xs" src="https://img.icons8.com/color/36/000000/administrator-male.png" alt="..."/> <input id="input" className="publisher-input" type="text" placeholder="Write something"> <span className="publisher-btn file-group"> <i className="fa fa-paperclip file-browser"></i> </span> <a className="publisher-btn" href="#" data-abc="true"><i className="fa fa-smile"></i></a> <a className="publisher-btn text-info" href="#" data-abc="true"><i className="fa fa-paper-plane"></i></a></input> </div>
            </div>
          </div>
        </div>
      </div>
    </div>
      /*
      <div id="container" classNameName="container">
        <div id="chat" classNameName="chat">
          <div id="messages" classNameName="messages"></div>
          <input id="input" type="text" placeholder="Say something..." autoComplete="off" autoFocus={true} />
        </div>
      </div>*/
		);
}

export default ReactChatBot;
